import '../styles/Footer_style.scss';
import logo from '../assets/Logos/isotipo.png';
import { Link } from 'react-router-dom';

const Footer = () => {
  return (
    <footer>
      <section className='footer-left'>
        <div className='logo-footer-container'>
          <img src={logo} alt='Logo de BeermoneyBot 2020' />
          <div className='socials'>
            <a href={`https://x.com/ombottrade`} target='_blank' rel='noreferrer'>
              <i className='fab fa-twitter'></i>
            </a>
            <a href={`https://medium.com/ombottrade`} target='_blank' rel='noreferrer'>
              <i className='fab fa-medium-m'></i>
            </a>
            <a href={`https://instagram.com/ombottrade`} target='_blank' rel='noreferrer'>
              <i className='fab fa-instagram'></i>
            </a>
          </div>
        </div>
      </section>
      <section className='footer-rigth'>
        <div className='logo-footer-container'>
          <p>Om Bot Trade: Monthly subscription for automated crypto trading on Binance. Transparent, non-custodial, past performance available, no guaranteed future returns.</p>
          <div className='footer-menu'>
            <ul>
              <li>
                <Link to='/'>Home</Link>
              </li>
               <li >
                <Link to='#'>Plans</Link>
              </li> 
               <li >
                <Link to='#'>Blog</Link>
              </li> 
               <li >
                <Link to='#'>FAQ</Link>
              </li> 
            </ul>
          </div>
        </div>
      </section>
    </footer>
  );
};

export default Footer;
