import React from 'react'; 
import Header from '../components/Header';
import ContainerMain from '../containers/ContainerMain';
import InnerSectionPerformance from '../components/InnerSectionPerformance';
import InnerSectionPlans from '../components/InnerSectionPlans';
import InnerSectionCalculator from '../components/InnerSectionCalculator';
import InnerSectionSpirit from '../components/InnerSectionSpirit';
import Footer from '../components/Footer';
import { MetaTags } from 'react-meta-tags';
import imagecard from '../assets/imagecard.jpg';

const Home = () => {
  return (
    <>
      <MetaTags>
        <title>Home | Om Bot</title>
        <meta name='title' content='Om Bot' />
        <meta property='og:title' content='Om Bot' />
        <meta name='og:description' content='Invest with peace of mind' />
        <meta name='description' content='Invest with peace of mind' />

        <meta property='og:type' content='website' />
        <meta property='og:url' content='https://ombottrade.com/' />
        <meta property='og:description' content='Invest with peace of mind' />

        <meta property='og:image' content={imagecard} />
        <meta property='twitter:image' content={imagecard} />

        <meta property='twitter:card' content='summary_large_image' />
        <meta property='twitter:url' content='https://ombottrade.com/' />
        <meta property='twitter:title' content='Om Bot' />
        <meta property='twitter:description' content='Invest with peace of mind' />
      </MetaTags>
      <Header />
      <ContainerMain home=''>
        <InnerSectionPerformance />
        <InnerSectionPlans />
        <InnerSectionCalculator />
        <InnerSectionSpirit />
      </ContainerMain>
      <Footer />
    </>
  );
};

export default Home;
