import '../styles/InnerSectionPlans_style.scss';
import { AnimationOnScroll } from 'react-animation-on-scroll';

const SectionUse = () => {
  return (
    <section className='main-container-use'>
      <div className='main-container-use-background'>
        <div className='title-container'>
          <div className='title-container-aux'>
            <AnimationOnScroll animateIn='animate__bounceIn' animateOnce={true}>
              <h2>Calculadora de Rendimientos</h2>
            </AnimationOnScroll>
          </div>
        </div>
        <section className='section-calculator'>
          <form action="/action_page.php">
            <label >Fondos iniciales:</label><br/>
            <input type="text" id="fname" name="fname" defaultValue="100"/><br/>
            <label >Meses a Proyectar:</label><br/>
            <input type="text" id="lname" name="lname" defaultValue="5"/><br/>
            <label >Fondos a Alcanzar:</label><br/>
            <input type="text" id="lname" name="lname" defaultValue="300"/><br/><br/>
            <input type="submit" value="Submit"/>
          </form> 
        </section>
      </div>
    </section>
  );
};

export default SectionUse;
