import React from 'react'
import Header from '../components/Header';
import ContainerText from '../containers/ContainerText';
import Footer from '../components/Footer';
import {MetaTags} from 'react-meta-tags';


const Team = () => {
  return (
    <>
      <MetaTags>
        <title>Autobalance | Om Bot</title>
        <meta property="og:title" content="Autobalance | Om Bot"/>
      </MetaTags>
      <Header small={'small'} />
      <ContainerText h1='Autobalance'>
        <p>En <strong>Om Bot Trade</strong>, la funcionalidad de auto balanceo es una herramienta clave para optimizar tu cartera de criptomonedas. Este proceso autom&aacute;tico se activa a partir del nivel <em>Adept</em>, brind&aacute;ndote la capacidad de gestionar tus activos de manera eficiente. Cuando el valor de BTC en la cartera representa menos del 25% de tu carteral, el sistema autom&aacute;ticamente balancea hacia BTC para aprovechar la volatilidad. De manera similar, cuando BTC supera el 50% del valor total, se realiza un ajuste hacia USDT hasta que el BTC vuelva al 25% del valor total de la cartera para mitigar riesgos.</p>
        <p>Estos porcentajes son configurables seg&uacute;n tus preferencias desde el dashboard de Om Bot Trade, permiti&eacute;ndote adaptar la estrategia a tu tolerancia al riesgo. La flexibilidad va m&aacute;s all&aacute;, ya que puedes operar tanto con USDT como con BTC como colateral en las transacciones, aprovechando las oportunidades del mercado de manera integral. Con Om Bot Trade, no necesitas conocimientos t&eacute;cnicos; simplemente personaliza los valores seg&uacute;n tu estrategia, o d&eacute;jalos en defecto, y deja que la automatizaci&oacute;n trabaje por ti.</p>
      </ContainerText>
      <Footer />
    </>
  )
}

export default Team
