import React from 'react'
import Header from '../components/Header';
import ContainerText from '../containers/ContainerText';
import Footer from '../components/Footer';
import {MetaTags} from 'react-meta-tags';


const Team = () => {
  return (
    <>
      <MetaTags>
        <title>Conditions | Om Bot</title>
        <meta property="og:title" content="Conditions | Om Bot"/>
      </MetaTags>
      <Header small={'small'} />
      <ContainerText h1='Conditions'>
      </ContainerText>
      <Footer />
    </>
  )
}

export default Team
