import React from 'react'
import Header from '../components/Header';
import ContainerText from '../containers/ContainerText';
import Footer from '../components/Footer';
import {MetaTags} from 'react-meta-tags';


const Team = () => {
  return (
    <>
      <MetaTags>
        <title>Strategy | Om Bot</title>
        <meta property="og:title" content="Strategy | Om Bot"/>
      </MetaTags>
      <Header small={'small'} />
      <ContainerText h1='Strategy'>
        El algoritmo de Om Bot Trade revoluciona el trading al prescindir de indicadores convencionales como RSI o MACD. En cambio, se sumerge en aspectos avanzados del mercado, analizando pools de liquidez, manipulaciones de mercados, noticias relevantes y cambios significativos en el volumen y número de órdenes. Esta sofisticada estrategia se implementa actualmente exclusivamente en Binance, permitiendo que cada operación se beneficie de un análisis exhaustivo y en tiempo real. Con un enfoque técnico detallado, nuestro algoritmo busca oportunidades donde otros no llegan, generando operaciones fundamentadas en datos más amplios y precisos para optimizar el rendimiento de tu cartera. Con Om Bot Trade, confía en una estrategia de trading vanguardista respaldada por la más avanzada tecnología financiera.
      </ContainerText>
      <Footer />
    </>
  )
}

export default Team
