import '../styles/MenuHeader_style.scss';
import { Link } from 'react-router-dom';

import logo from '../assets/Logos/isotipo trans.png';

export const MenuHeader = () => {
  return (
    <>
      <div className='logo-container'>
        <img className='logo-menu' src={logo} alt=''></img>
      </div>
      <div className='menu-container'>
        <ul className='main-menu'>
          <li className='menu-item'>
            <Link to='/'>Home</Link>
          </li>
           <li className='menu-item hasmenu'>
            <Link to='/features'>Features</Link>
              <ul className='submenu'>
                <li className='menu-item'>
                  <Link to='/strategy'>Strategy</Link>
                </li> 
                <li className='menu-item'>
                  <Link to='/technology'>Technology</Link>
                </li> 
                <li className='menu-item'>
                  <Link to='/autobalance'>Autobalance</Link>
                </li> 
                <li className='menu-item'>
                  <Link to='/api-keys'>API Keys</Link>
                </li> 
              </ul>
          </li> 
           <li className='menu-item'>
            <Link to='/performance'>Performance</Link>
          </li> 
           <li className='menu-item'>
            <Link to='/plans'>Plans</Link>
          </li> 
           <li className='menu-item'>
            <Link to='/blog'>Blog</Link>
          </li> 
           <li className='menu-item'>
            <Link to='/faq'>FAQ</Link>
          </li> 
           <li className='menu-item'>
            <Link to='https://app.ombottrade.com'>Access</Link>
          </li> 
        </ul>
      </div>
    </>
  );
};
