import '../styles/InnerSectionPlans_style.scss';
import { AnimationOnScroll } from 'react-animation-on-scroll';

const SectionUse = () => {
    return (
        <section className='main-container-use'>
      <div className='main-container-use-background'>
        <div className='title-container'>
          <div className='title-container-aux'>
            <AnimationOnScroll animateIn='animate__bounceIn' animateOnce={true}>
              <h2>Comienza sin Compromisos</h2>
              <p>¿Nuevo en esto? Comienza con nuestro plan Novice Monk, sin costo inicial y con la posibilidad de iniciar con una cartera pequeña.</p>
            </AnimationOnScroll>
          </div>
        </div>
        <section className='section-plans'>
          <AnimationOnScroll animateIn='animate__fadeInDown' animateOnce={true}>
            <div className='grid-plans'>
              <table >
                <thead>
                  <tr>
                    <th>Nivel</th>
                    <th>Minimo fondo en Cartera</th>
                    <th>Maximo fondo en Cartera</th>
                    <th>Costo Mensual</th>
                  </tr>
                </thead>
                <tbody>
                <tr>
                  <td>Novice Monk</td>
                  <td>0$</td>
                  <td>100$</td>
                  <td>0$</td>
                </tr>
                </tbody>
              </table>

            </div>
          </AnimationOnScroll>
        </section>
      </div>
    </section>
    );
};

export default SectionUse;