import React from "react";
import '../styles/InnerSectionPerformance_style.scss';
import { AnimationOnScroll } from 'react-animation-on-scroll';
import Chart from "react-apexcharts";


const SectionWhat = () => {

    let example = {
      options: {
        chart: {
          id: 'apexchart-example'
        },
        xaxis: {
          categories: [1991, 1992, 1993, 1994, 1995, 1996, 1997, 1998, 1999]
        }
      },
      series: [{
        name: 'series-1',
        data: [30, 40, 35, 50, 49, 60, 70, 91, 125]
      }]
    }


    return (
        <section className='main-container-performance'>
      <div className='title-container '>
        <div className='title-container-aux'>
          <AnimationOnScroll animateIn='animate__bounceIn' animateOnce={true}>
            <h2 className=''>Rendimientos Asombrosos</h2>
            <p>Observa la gráfica de rendimientos del último mes de la cuenta demo y sumérgete en nuestro historial de éxitos.</p>
          </AnimationOnScroll>
          <div id='chart'></div>
        </div> 
      </div>
      <section className='container-articles-performance'>
        <Chart options={example.options} series={example.series} type="bar" width={500} height={320} />
      </section>
    </section>
    );
};

export default SectionWhat;